import React, { useState, useEffect } from "react";
import CreateMeeting from "./components/CreateMeeting";
import FullSchedule from "./components/FullSchedule";

export default function App() {
  let [schedule, setSchedule] = useState([]);

  useEffect(() => {
    async function getSchedule() {
      const scheduleFromServer = await fetchSchedule();
      setSchedule(scheduleFromServer);
    };
    getSchedule();
  }, [])

  async function fetchSchedule() {
    const res = await fetch(
      "https://my-json-server.typicode.com/vincentktieu-ucsb/cs185-pr4-server/schedule", {
        method: "GET"
      }
    );
    return await res.json();
  }

  async function deleteMeeting(id) {
    const res = await fetch(
      `https://my-json-server.typicode.com/vincentktieu-ucsb/cs185-pr4-server/schedule/${id}`
    );
    schedule.splice(id,1);
    setSchedule([...schedule]);
  }

  const addMeeting = async (item) => {
    const res = await fetch(
      "https://my-json-server.typicode.com/vincentktieu-ucsb/cs185-pr4-server/schedule", {
        method: "POST",
        headers: {
          "Content-type": "application/json",
        },
        body: JSON.stringify(item),
      }
    );
    const data = await res.json();
    setSchedule([...schedule, item]);
  }

  return (
    <div className="container">
      <header>
        <h3 className="nav-brand">Vincent Tieu's PA4</h3>
      </header>
      <div className="dual-section-display">
        <div className="dual-section">
          <FullSchedule schedule={schedule} deleteMeeting={deleteMeeting} />
        </div>
        <div className="dual-section">
          <CreateMeeting addMeeting={addMeeting} />
        </div>
      </div>
    </div>
  );
}
